 .controle:hover{
  color: var(--quarta-color);
  font-size: 2rem;
  border: 2px solid var(--quarta-color);
  background-color: var(--primeira-color);
  padding: 4px 4rem;
 }
 .controle{
  font-family: var(--segunda-font);
  text-decoration: none;
  color: var(--primeira-color);
  font-size: 1.9rem;
  border-radius: 5px;
  background-color: var(--quarta-color);
  border: 2px solid var(--quarta-color);
  padding: 3px 4rem;
  cursor: pointer;
  transition: .5s;
 }

 