@media (min-width:200px){
    .container{
        display: block;
        justify-content: space-between;
        margin-top: 8%;
    }
    .centra{
        width: 100%;
        background-color:var(--primeira-color);
        padding: 2rem;
        margin-bottom: 2rem;
    }
    .ico{
        background-color: rgba(0, 0, 0, 0.356);
        padding: 3rem;
        margin: auto;
        margin-bottom: 2rem;
        width: 100%;
    }
    .texCentral{
        width: 100%;
    }
    .container .texCentral img{
        width: 130%;
        margin-left: -30%;
    }
    .container .texCentral h1{
       font-size: 1.3rem;
    }
    .texCentral span{
        margin: auto;
        margin-left: 1rem;
       font-family: var(--font-principal);
       color: var(--quarta-color);
       font-size: 2rem;
       background-color: var(--primeira-color);
       padding: .5rem;
       border-radius: 5px;
       box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.274);
       cursor: pointer;
    }
    .texCentral span:hover{
       background-color: var(--quarta-color);
       font-size: 2rem;
       color: var(--primeira-color);
       padding: .8rem;
       border-radius: 5px;
       box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.274);
    }
    .centra label{
        font-family: Arial, Helvetica, sans-serif;
       color: var(--quarta-color);
       font-size: 1rem;
       font-weight: 800;
       text-align: initial;
    }
    .container .texCentral h4{
        margin-bottom: 1rem;
        margin-top: 0px;
        font-size: 2.3rem;
        color: var(--primeira-color);
    }
    .cont .form form{
        width: 100%;
    }
    .cont .form form span{
        font-size: 1rem;
        margin: 0px;
        padding: .5rem;
    }
    .cont .form form input{
        width: 40%;
        border-radius: 2rem;
        text-align: center;
        border: .2px solid rgba(0, 0, 0, 0.281);
    }
    .cont .form form textarea{
        width: 40%;
    } 
}
@media (min-width:480px){
    .container .texCentral h4{
        margin-bottom: 2rem;
        margin-top: 4rem;
        font-size: 2.9rem;
    }
    .container .texCentral h1{
        font-size: 1.9rem;
     }
     .texCentral span{
        font-size: 2.4rem;
     }
     .texCentral span:hover{
        font-size: 2.4rem;
     }
}
@media (min-width:768px){
    .container{
        display: flex;
        justify-content: space-between;
        margin-top: 11%;
    }
    .container .texCentral h4{
        margin-bottom: 2rem;
        font-size: 3.5rem;
    }
    .container .texCentral h1{
        font-size: 1.9rem;
     }
     .texCentral span{
        font-size: 2.7rem;
     }
     .texCentral span:hover{
        font-size: 2.7rem;
     }
}
@media (min-width:1024px){
    .texCentral span{
       font-size: 3rem;
    }
    .texCentral span:hover{
       font-size: 3rem;
    }
    .container .texCentral h4{
        margin-bottom: 2rem;
        margin-top: 0px;
        font-size: 4rem;
    }
}

