.responsivo{
    display:none;
    cursor: pointer;
}
.div{
    background-color:transparent;
    position: fixed;
    top: 0;
    z-index: 1000;
    
}
nav{ 
    display: flex;
    flex-wrap: nowrap;
    align-content: center;
    justify-content: space-between;
}
.menu{
    display: flex;
    flex-direction: row;
    align-content: center;
    align-items: center;
    justify-content: space-between;
    background-color: var(--quinta-color);
    border-radius: 0px 0px 10px 10px;
    margin-right: -90%;
    height: 60px;
    position: relative;
    box-sizing: border-box;
    overflow: visible;
    visibility: inherit;
    box-shadow: 4px 4px 7px rgba(98, 166, 175, 0.397);
    z-index: 2000;
}
nav a{
    font-family: Arial, Helvetica, sans-serif;
    text-decoration: none;
    color: var(--primeira-color);
    font-size: 1.1rem;
    text-align: center;
    line-height: 45px;
    padding: 10px;
    transition: .5s;
}
nav a:hover{
    background-color: var(--primeira-color);
    color: var(--quarta-color);
   
}
.serviços h3{
    font-family: var(--segunda-font);
    border: none;
    color: var(--quarta-color);
    font-size: 1.9rem;
    z-index: 1000;
    font-weight: bolder;
    background-color: var(--primeira-color);
    border-radius: 0px 0px 10px 10px;
    margin-left: -100%;
    width: 200px;
    height: 60px;
    position: relative;
    box-sizing: border-box;
    overflow: visible;
    visibility: inherit;
    box-shadow: 4px 4px 7px rgba(98, 166, 175, 0.397);
    transition: .5s;
}
.serviços  h3:hover{
    font-size: 2.1rem;
    background-color: var(--quarta-color);
    font-family: var(--segunda-font);
    color: var(--primeira-color);
}
.serviços  a{
    font-size: 1.2rem;
    cursor: pointer;
   z-index: 1000;
}
.men button{
    font-family: var(--segunda-font);
    border: none;
    color: var(--quarta-color);
    z-index: 1000;
    font-weight: bolder;
    background-color: var(--primeira-color);
    border-radius: 0px 0px 10px 10px;
    margin-left: 4%;
    width: 115px;
    height: 35px;
    position: relative;
    box-sizing: border-box;
    overflow: visible;
    visibility: inherit;
    box-shadow: 4px 4px 7px rgba(98, 166, 175, 0.397);
    transition: .5s;
}
.men  a{
    font-size: 1rem;
    cursor: pointer;
   z-index: 1000;
}
.men a:hover{
    font-size: 1.1rem;
    color: var(--primeira-color);
    padding: .5px;
    border-radius: 3px;
}
.men div ul a{
    display: block;
    background-color: rgba(255, 255, 255, 0.651);
    margin: 0px;
    border-radius: 4px
}
.men div ul a:hover{
    font-size: 1.2rem;
    color: aliceblue;
    border-bottom: 2px solid aliceblue;
    display: block;
    background-color: rgba(0, 0, 0, 0.473);
    margin: 0px;
}
.men  p{
    font-size: 1.2rem;
    font-weight: bolder;
    background-color: var(--primeira-color);
    font-family: var(--segunda-font);
    color: var(--quarta-color);
    line-height: 25px;
    padding: 4px;
    border-radius: 0px 0px 10px 10px;
}
.men  p:hover{
    font-size: 1.3rem;
    background-color: var(--quarta-color);
    font-family: var(--segunda-font);
    color: var(--primeira-color);
    line-height: 25px;
    padding: 4px;
}
.destaque {
    font-size: 1.7rem;
    font-weight: bolder;
    background-color: var(--primeira-color);
    font-family: var(--segunda-font);
    color: var(--quarta-color);
    line-height: 45px;
    padding: 10px;
    border-radius: 0px 0px 0px 10px;
     transition: .5s;
}
.menu .destaque:hover{
    background-color: var(--quarta-color);
    font-family: var(--segunda-font);
    color: var(--primeira-color);
    border-radius: 0px 0px 0px 10px;
}
.serviços a:hover{
    font-size: 1.3rem;
    color: var(--primeira-color);
    padding: .5px;
    border-radius: 3px;
}
@media (min-width:200px){
    .responsivo{
        display:block;
    }
    .serviços h3{
        display: none;
    }
    .menu{
        display: none;
    }
 
}
@media (min-width:480px){
    .men button{
        width: 170px;
        height: 60px;
    }
    .men  a{
        font-size: 1.2rem;
    }
    .men a:hover{
        font-size: 1.3rem;
    }
    .men  p{
        font-size: 1.5rem;
        line-height: 50px;
        padding: 5px;
    }
    .men  p:hover{
        font-size: 1.6rem;
        line-height: 50px;
        padding: 5px;
    }
}
@media (min-width:768px){ 
    .responsivo{
        display:none;
    }
    .serviços h3{
        display: block;
        margin-left: -21%;
    }
    .menu{
        display: flex;
        margin-right: -14%;
    }
}
@media (min-width:890px){   
    .serviços h3{
        margin-left: -46%;
    }
    .menu{
        display: flex;
        margin-right: -31%;
    }
}
@media (min-width:1024px){   
    .serviços h3{
        margin-left: -76%;
    }
    .menu{
        display: flex;
        margin-right: -50%;
    }
}
@media (min-width:1224px){   
    .serviços h3{
        margin-left: -120%;
    }
    .menu{
        display: flex;
        margin-right: -81%;
    }
}
