@charset "UTF-8";
@import url('https://fonts.googleapis.com/css2? family= Playfair+Display:ital,wght@1,400;1,500 & display=swap');
@import url('https://fonts.googleapis.com/css2? family= Lora:ital@1 & display=swap');

*{
    padding: 0px;
    margin: 0px;
    box-sizing: border-box;
}
:root{
    --sombra: rgba(0, 0, 0, 0.13);
    --quarta-color: #CBD9AD;
    --terceira-color: #713A1B;
    --quinta-color: #FfFfFf;
    --sexta-color: #7BA88B;
    --segunda-color: #7897BF;
    --setima-color: #142050;
    --primeira-color: #2C334E;

    --mob1:.8rem;
    --mob2:.9rem;  

    --tab1:1rem; 
    
    --hmob: 1.1rem;
    --hmob2: 1.2rem;
    --hmob3: 1.3rem;

    --htab1: 1.4rem;
    --htab1: 1.6rem;
    --htab1: 1.8rem;

    --font-principal: 'Suez One', serif;
    --segunda-font: 'Dancing Script', 'cursive';
}
html, body, #root{
    text-align: center;
  
}

h1{
    color: var(--primeira-color);
    font-family: var(--font-principal);
    padding: 10px;
    margin: 5px 0px;
}
h2{
    color: var(--setima-color);
    font-weight: 300;
    font-family: var(--font-principal);
    padding: 8px;
    margin: 4px 0px;
}
h3{
    color: var(--terceira-color);
    font-family: var(--segunda-font);
    padding: 6px;
    margin: 3px 0px;
}h4{
    color: var(--quinta-color);
    font-family: var(--segunda-font);
    padding: 6px;
    margin: 3px 0px;
}
p{
    color: var(--segunda-color);
    font-family:Arial, Helvetica, sans-serif;
    padding: 4px;
    margin: 2px 0px;
    font-family: 'Lora', serifa;
    text-align: center;
}
a{
    color: var(--quinta-color);
    text-decoration: none;
}
a:hover{
    color: var(--primeira-color);
}
