
.a{
    font-family: var(--segunda-font);
    text-decoration: none;
    color: var(--quarta-color);
    font-size: 2rem;
    border-radius: 5px;
    background-color: var(--primeira-color);
    border: 2px solid var(--primeira-color);
    padding: 7px;
    cursor: pointer;
    transition: .5s;
}
.a:hover{
    color: var(--setima-color);
    font-size: 2.1rem;
    border: 2px solid var(--primeira-color);
    background-color: var(--quarta-color);
    padding: 8px;
}
@media (min-width:200px){
    .a{
        font-size: 1.3rem;
        padding: 4px;
    }
    .a:hover{
        font-size: 1.4rem;
        padding: 5px;
    }
 }
 @media (min-width:480px){
    .a{
        font-size: 1.5rem;
        padding: 5px;
    }
    .a:hover{
        font-size: 1.6rem;
        padding: 6px;
    }
 }
 @media (min-width:768px){
    .a{
        font-size: 1.8rem;
        padding: 6px;
    }
    .a:hover{
        font-size: 1.9rem;
        padding: 7px;
    }
 }
 @media (min-width:1024px){
    .a{
        font-size: 1.9rem;
        padding: 7px;
    }
    .a:hover{
        font-size: 2rem;
        padding: 8px;
    }
 }