.body{
    background-image: url("../../midias/8.png");
    background-size: contain;
    width: 100%;
    height: 755px;
    position: absolute;
    background-repeat: no-repeat;
}
.texCentral{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 16%;
    margin-left: 10%;
    flex-wrap: wrap;
    align-content: flex-start;
}
.texCentral h1{
   font-family: var(--font-principal);
   color: var(--primeira-color);
   font-size: 3rem;
   margin: 0px;
   padding-bottom: 0px;
}
.ico{
    display: flex;
    gap: 10px;
    margin-top: 2%;
}
.texCentral span{
   font-family: var(--font-principal);
   color: var(--quarta-color);
   font-size: 4.5rem;
   background-color: var(--primeira-color);
   padding: .5rem;
   border-radius: 10px;
   box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.274);
   cursor: pointer;
}
.texCentral span:hover{
   background-color: var(--quarta-color);
   font-size: 4.5rem;
   color: var(--primeira-color);
   padding: .8rem;
   border-radius: 10px;
   box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.274);
}
.texCentral p{
   font-family: Arial, Helvetica, sans-serif;
   color: var(--setima-color);
   font-size: 2rem;
   margin: 0px;
   padding-top: 0px;
   text-align: initial;
}
.sobre{
    width: 70%;
    margin: 7% auto;
    margin-bottom: 3rem;
}
.sobre p{
    font-family: Arial, Helvetica, sans-serif;
    font-size: 1.3rem;
    margin: 2rem;
}
.sobre h1{
    font-family: var(--font-principal);
    color: var(--primeira-color);
    font-size: 2.5rem;
    margin: 2px;
 }
 @media (min-width:200px){
    .body{
       background-image: none;
        position: relative;
    }
    .back{
        background-image: url('../../midias/9.png');
        background-repeat: no-repeat;
        background-size: contain;
    }
    .texCentral{
        margin-top: 13%;
        margin-left: 40%;
        align-items: center;
    }
    .texCentral h1{
       font-size: 1.4rem;
       margin: 0px;
       padding-bottom: 0px;
    }
    .texCentral span{
        font-size: 2rem;
        padding: .5rem;
        border-radius: 7px;
     }
     .texCentral span:hover{
        font-size: 2rem;
        padding: .8rem;
     }
     .texCentral p{
        font-size: .8rem;
     }
     .sobre{
         width: 93%;
         margin: 7% auto;
         margin-bottom: 1.5rem;
     }
     .sobre p{
         font-size: 1rem;
         margin: 1rem;
     }
     .sobre h1{
         font-size: 1.1rem;
         margin: 2px;
     }
     .sobre h2{
         font-size: 1rem;
         margin: 2px;
     }
 }
