.controle{
    display: flex;
    flex-direction: column;
    margin-bottom: .5rem;
}
.controle label{
    margin-bottom: .4rem;
    font-weight: bold;
}
input{
    padding: 7px;
    border-radius: 3px;
    border: none;
    text-align: center;
}
.cotrole input::placeholder{
    color: var(rgba(0, 0, 0, 0.048));
}