.controle{
    display: flex;
    flex-direction: column;
    margin-bottom: 1rem;
}
.controle label{
    margin-bottom: .6rem;
    font-weight: bold;
}
textarea{
    padding: 2.7rem;
    border-radius: 0px;
    border: none;
    text-align: center;
}
.cotrole textarea::placeholder{
    color: var(#0000007e);
}