
@media (min-width:200px){
  .ma .fundo{
    background-color: rgba(0, 0, 0, 0.247);
    display: block;
    margin-left: -1%;
    padding: 10px;
  }
  .ma .fundo div img{
    border-radius: 10px;
    width: 95%;
    box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.185);
    cursor: pointer;
  }
  .ma .fundo div img:hover{
    width: 100%;
  }
  .ma .fundo h3{
    font-size: 1.5rem;
  }
  .ma .fundo h3:hover{
    font-size: 1.6rem;
  }
  .rede{
    width: 93%;
    padding: 15px;
    margin: auto;
  }
  .rede p{
    font-size: .9rem;
    font-family: Arial, Helvetica, sans-serif;
    color:aliceblue;
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.062);
    text-align: justify;
  }
  .rede p:hover{
    font-size: 1rem;
  }
  .rede h3 a{
    color: var(--primeira-color);
  }
  .link{
    margin-top: 1rem;
    display: flex;
    flex-wrap: nowrap;
    justify-content:center;
    align-items: center;
    gap: 1.5rem;
  }
  .link a{
    text-decoration: none;
  }
  .git{
    background-color: var(--primeira-color);
    border: 1px solid white;
    color: aliceblue;
    padding:.5rem 2rem;
    border-radius: 5px;
    width: 170px;
    box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.144);
    cursor: pointer;
  }
  .git:hover{
    padding:.8rem 2.5rem;
  }
  .git a{
    color: aliceblue;
  }
  .site{
    background-color: white;
    border: 1px solid var(--primeira-color);
    color: var(--primeira-color);
    padding: .5rem 2rem;
    border-radius: 5px;
    width: 170px;
    box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.144);
    cursor: pointer;
  }
  .site:hover{
    padding:.8rem 2.5rem;
  }
}
@media (min-width:480px){ 
  .ma .fundo h3{
    font-size: 1.7rem;
  }
  .ma .fundo h3:hover{
    font-size: 1.8rem;
  }
  .rede p{
    font-size: 1rem;
  }
  .rede p:hover{
    font-size: 1.1rem;
  }
  .rede{
    width: 83%;
    padding: 15px;
    margin: auto;
  }
}
@media (min-width:768px){
  .ma .fundo{
    background-color: rgba(0, 0, 0, 0.247);
    display: flex;
    margin-left: -1%;
    padding: 10px;
  }
  .ma .fundo h3{
    font-size: 1.9rem;
  }
  .ma .fundo h3:hover{
    font-size: 2rem;
  }
  .rede p{
    font-size: 1rem;
  }
  .rede p:hover{
    font-size: 1.1rem;
  }
}
