
@media (min-width:200px){
   .sobre{
      width: 90%;
      margin: auto;
  }
  .sobre p{
      font-size: 1rem;
      margin: 2rem 0px;
  }
  .sobre h1{
      font-family: var(--font-principal);
      color: var(--primeira-color);
      font-size: 1.5rem;
      margin: 2px;
   }
   .sobre h2{
      padding-top: 15%;
      font-size: 1.2rem;
   }
   .card{
      display: block;
      padding: 0px;
   }
   .card .tex{
      padding: 0%;
   }
   .card .tex p{
     font-family: Arial, Helvetica, sans-serif;
     font-size: 1rem;
     margin: 2rem 0px;
  }
   .card div img{
     width: 40%;
     margin-top: 3rem;
     margin-left: -60%;
   }
}
@media (min-width:480px){
   .sobre{
      width: 83%;
      margin: auto;
  }
  .sobre p{
      font-size: 1.1rem;
      margin: 2rem 0px;
  }
  .sobre h1{
      font-family: var(--font-principal);
      color: var(--primeira-color);
      font-size: 2rem;
      margin: 2px;
   }
   .sobre h2{
      padding-top: 15%;
      font-size: 1.5rem;
   }
   .card .tex p{
     font-size: 1.1rem;
     margin: 2rem 0px;
  }
   .card div img{
     width: 40%;
     margin-top: 3rem;
     margin-left: -60%;
   }
  
}
@media (min-width:768px){
   .sobre{
      width: 75%;
      margin: auto;
  }
  .sobre p{
      font-size: 1.2rem;
      margin: 2rem 0px;
  }
  .sobre h1{
      font-family: var(--font-principal);
      color: var(--primeira-color);
      font-size: 2.3rem;
      margin: 2px;
   }
   .sobre h2{
      padding-top: 15%;
      font-size: 1.8rem;
   }
   .card{
      display: flex;
      align-items: center;
   }
   .card .tex p{
     font-size: 1.2rem;
     margin: 2rem 0px;
     padding: 1rem;
  }
   .card div img{
     width: 110%;
     margin-top: 3rem;
     margin-left: 1%;
   }
}
@media (min-width:1024px){
   .sobre{
      width: 70%;
      margin: auto;
  }
  .sobre p{
      font-size: 1.3rem;
      margin: 2rem;
  }
  .sobre h1{
      font-family: var(--font-principal);
      color: var(--primeira-color);
      font-size: 2.5rem;
      margin: 2px;
   }
   .sobre h2{
      padding-top: 10%;
   }
   .dv{
      background-image: linear-gradient(to right,#283f9b1e, #cbd9ad48, #283f9b1e  );
   }
   .card{
      display: flex;
      align-items: center;
      padding: 15px;
   }
   .card .tex{
      padding: 10%;
   }
   .card .tex p{
     font-family: Arial, Helvetica, sans-serif;
     font-size: 1.3rem;
     margin: 2rem;
  }
   .card div img{
     width: 160%;
   }
  
}