
.div{
    display: flex;
    background-color: var(--primeira-color);
    justify-content: center;
    padding: 10px;
}
.div h5{
    font-size: 2.5rem;
    color: var(--quarta-color);
}
.div .ico{
    display: flex;
    justify-content: center;
    gap: 10px;
}
.texCentral span{
   font-family: var(--font-principal);
   color: var(--quarta-color);
   font-size: 2rem;
   background-color: var(--primeira-color);
   padding: .5rem;
   border-radius: 3px;
   box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.274);
   cursor: pointer;
}
.texCentral span:hover{
   background-color: var(--quarta-color);
   font-size: 2rem;
   color: var(--primeira-color);
   padding: .6rem;
   box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.274);
}
.texCentral h2{
    color: aliceblue;
}
@media (min-width:200px){
    .texCentral span{
       font-size: 1.5rem;
       padding: .4rem;
       border-radius: 3px;
    }
    .texCentral span:hover{
       font-size: 1.6rem;
       padding: .5rem;
    }
    .texCentral h2{
        font-size: 1.3rem;
        padding: 0px;
    }
}
@media (min-width:480px){
    .texCentral span{
        font-size: 1.7rem;
        padding: .4rem;
        border-radius: 5px;
     }
     .texCentral span:hover{
        font-size: 1.8rem;
        padding: .5rem;
     }
     .texCentral h2{
         font-size: 1.3rem;
         padding: 0px;
     }
 }
@media (min-width:768px){
    .texCentral span{
        font-size: 2rem;
        border-radius: 4px;
     }
     .texCentral span:hover{
        font-size: 2.1rem;
     }
     .texCentral h2{
         font-size: 1.5rem;
         padding: 0px;
     }
 }
@media (min-width:1024px){
    .div{
        display: flex;
        background-color: var(--primeira-color);
        justify-content: center;
        padding: 10px;
    }
    .div h5{
        font-size: 2.5rem;
        color: var(--quarta-color);
    }
    .div .ico{
        display: flex;
        justify-content: center;
        gap: 10px;
    }
    .texCentral span{
       font-family: var(--font-principal);
       color: var(--quarta-color);
       font-size: 2rem;
       background-color: var(--primeira-color);
       padding: .5rem;
       border-radius: 5px;
       box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.274);
       cursor: pointer;
    }
    .texCentral span:hover{
       font-size: 2rem;
       padding: .6rem;
    }
    .texCentral h2{
        color: aliceblue;
    }
}